.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

section {
  background-color: var(--section-color);
  display: flex;
  flex-direction: column;
  width: 1440px;
  height: 1024px;
  background: #ffffff;
  margin: auto;
  position: relative;
  left: var(--left-margin);
}
div.description {
  position: absolute;
  width: 580px;
  height: 260px;
  top: 300px;
}
h1.title_text {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 64px;
  line-height: 77px;
  width: 600px;
  color: #000000;
  margin: 0px;
  border: 0px solid #000000;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  justify-self: end;
}
p.title_description {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 42px;
  width: 600px;
  color: #000000;
  margin: 10px 0px;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
